@-webkit-keyframes colorchange {
  from {
    background-color: var(--primary-color);
  }
  to {
    background-color: var(--secondary-color);
  }
}

@keyframes colorchange {
  from {
    background-color: var(--primary-color);
  }
  to {
    background-color: var(--secondary-color);
  }
}

.navbar {
  width: 100%;
  height: 4.5rem;
  position: fixed;
  background-color: var(--secondary-color);
  border-bottom: 2px solid var(--tritary-color);
}

.navbar .navbar-title {
  font-family: 'Big Shoulders Display', sans-serif;
  display: inline-block;
}

.navbar .navbar-title .text-p {
  -webkit-animation: navbar-title-color-change 5s;
          animation: navbar-title-color-change 5s;
}

ul.social-list {
  list-style: none;
}

.social-icon {
  width: 50px;
  aspect-ratio: 1;
}

.social-list-item {
  display: inline-block;
  position: relative;
  top: -10%;
  padding-right: 25px;
}

div.flex-contain {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  margin: 0;
}

.nav-icon-link {
  width: 60px;
  height: 60px;
  background: var(--primary-color);
  margin: 0 5px 16px 5px;
  border-radius: 10px;
  border: 2px solid var(--tritary-color);
  -webkit-transition: background-color 2s ease-out 100ms;
  transition: background-color 2s ease-out 100ms;
}

.nav-icon-link:hover {
  background-color: var(--secondary-color);
}

.nav-icons-bar {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: auto auto auto;
      grid-template-columns: auto auto auto;
}

.nav-icon {
  width: 50px;
  aspect-ratio: 1;
  display: block;
  margin: 5px auto;
}

@media (max-width: 700px) {
  .social-icon {
    width: 40px;
    aspect-ratio: 1;
  }
  .social-item-list {
    padding-right: 20px;
  }
  .nav-icon-link {
    width: 40px;
    height: 40px;
    margin-bottom: 10px;
  }
  .nav-icon {
    width: 33.3px;
    height: 33.3px;
  }
}

@media (max-width: 350px) {
  .navbar-title {
    font-size: 20px;
  }
  .navbar {
    height: 5rem;
  }
}

@media (max-width: 400px) {
  .social-icon {
    width: 30px;
    aspect-ratio: 1;
  }
  .social-item-list {
    padding-right: 15px;
  }
  .social-list {
    margin-bottom: -10px;
    margin-top: 0;
  }
  .navbar {
    height: 6rem;
  }
  .navbar-title {
    font-size: 30px;
  }
}

@media (max-width: 500px) {
  .navbar {
    height: 6rem;
  }
  ul.social-list {
    margin-bottom: 10px;
    margin-top: 10px;
    margin-right: 20px;
    padding-right: 10px;
    padding-left: 0;
    width: 1rem;
  }
  .flex-contain {
    padding-top: 2rem;
    height: 2rem;
  }
}
